export class AuthFrontend {
  private LOGIN_API = '/api/login';
  private modal: any = null;

  constructor() {
    console.log('AuthFrontend initialized');
  }

  init() {
    this.initModal();
    this.bindEvents();
    this.renderAuthState();
  }

  private $(sel: string) {
    return document.querySelector(sel);
  }

  private initModal() {
    const el = this.$('#authModal');
    // @ts-ignore
    this.modal = el ? new bootstrap.Modal(el) : null;
  }

  private bindEvents() {
    const form = document.querySelector('#login-form') as HTMLFormElement | null;
  document.querySelector('#login-form')?.addEventListener('submit', (e) => this.onLoginSubmit(e));
  document.querySelector('#register-form')?.addEventListener('submit', (e) => this.onRegisterSubmit(e));

  this.$('#btn-open-login')?.addEventListener('click', () => this.modal?.show());
  this.$('#btn-logout')?.addEventListener('click', () => this.doLogout());
  }

  private showMsg(msg: string, type: 'success' | 'danger' | 'info' = 'info') {
    const main = document.querySelector('main') || document.body;
    const div = document.createElement('div');
    div.className = `alert alert-${type} alert-dismissible fade show`;
    div.innerHTML = `${msg}<button type="button" class="btn-close" data-bs-dismiss="alert"></button>`;
    main.prepend(div);
    setTimeout(() => div.remove(), 4000);
  }

  private async doLogin(payload: { email: string; senha: string }) {
    const res = await fetch(this.LOGIN_API, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(payload),
    });

    if (!res.ok) {
      const body = await res.json().catch(() => null);
      throw new Error(body?.message || 'Erro no login');
    }

    return res.json();
  }

  private async onLoginSubmit(e: Event) {
    e.preventDefault();

    const form = e.target as HTMLFormElement;

    const email = (form.querySelector('[name="email"]') as HTMLInputElement).value.trim();
    const senha = (form.querySelector('[name="senha"]') as HTMLInputElement).value.trim();

    try {
      const result = await this.doLogin({ email, senha });
      console.log(result);
      localStorage.setItem('token', result.token);
      this.modal?.hide();
      window.location.href = "/";
    } catch (err: any) {
      this.showMsg(err.message, 'danger');
    }
  }
  
  private async onRegisterSubmit(e: Event) {
    e.preventDefault();

    const form = e.target as HTMLFormElement;

    const payload = {
      nome: (form.querySelector('[name="nome"]') as HTMLInputElement).value.trim(),
      email: (form.querySelector('[name="email"]') as HTMLInputElement).value.trim(),
      senha: (form.querySelector('[name="senha"]') as HTMLInputElement).value.trim(),
      data_nascimento: (form.querySelector('[name="data_nascimento"]') as HTMLInputElement).value.trim(),
    };

    try {
      const res = await fetch('/api/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload),
      });

      if (!res.ok) throw new Error('Erro ao criar conta');

      this.showMsg('Conta criada com sucesso!', 'success');
      this.modal?.hide();
    } catch (err: any) {
      this.showMsg(err.message, 'danger');
    }
  }

  private doLogout() {
    localStorage.removeItem('token');
    window.location.href = "/";
  }

  private renderAuthState() {
    const token = localStorage.getItem('token');
    const btnLogin = this.$('#btn-open-login') as HTMLElement;
    const btnLogout = this.$('#btn-logout') as HTMLElement;
    const btnManageSubjects = this.$('#btn-manage-subjects') as HTMLElement;
    const lblUser = this.$('#auth-username');

    if (!btnLogin || !btnLogout) return;

    if (!token) {
      btnLogin.style.display = '';
      btnLogout.classList.add('d-none');
      btnManageSubjects.classList.add('d-none');

      if (lblUser) lblUser.textContent = '';
      return;
    }

    const [, payloadBase64] = token.split('.');
    const json = JSON.parse(atob(payloadBase64));
    btnLogin.style.display = 'none';
    btnLogout.classList.remove('d-none');
    btnManageSubjects.classList.remove('d-none');
    if (lblUser) lblUser.textContent = `Olá, ${json?.nome ?? ''}`;
    
  }
}
